.card-container {
    display: flex;
    flex-direction: column;
    background:linear-gradient(to right,rgba(125, 221, 250, 0.75),rgba(226, 212, 212, 0.75));
    border: 1px solid rgb(131, 157, 243);
    box-shadow: 0px 10px 10px rgb(91, 99, 104);
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
    filter: contrast(150%);
  }
  
  .card-container:hover {
    transform: scale(1.05);
  }
  